<template>
  <div class="shopsignIn">
    <!--  -->
    <TopHead title="">
      <div slot="r" @click="submitSigin">
        <i class="icon iconfont icon-duihao"></i>
      </div>
    </TopHead>
    <!-- input -->
    <input
      type="file"
      @change="uploadImg"
      class="input-file"
      accept="image/*"
      ref="file"
      value="123"
    />
    <!--  -->
    <div class="img-box-wrap">
      <!--  -->
      <imgBtn
        @click="$refs.file.click()"
        class="img-box"
        iconclass="icon-xiangji"
        val=""
        :isRed="false"
      ></imgBtn>
      <!--  -->
      <div
        @click="img.checked = !img.checked"
        class="img-box"
        v-for="img in imgs"
        :key="img.url"
      >
        <!--  -->
        <div v-if="img.checked" class="imgchecked">
          <i class="icon iconfont icon-duihao"></i>
        </div>
        <img :src="img.url" alt="" width="100%" height="100%" />
      </div>
      <!--  -->
      <imgBtn
        @click="removeImg"
        class="img-box"
        iconclass="icon-shanchudange"
        val=""
        :isRed="true"
      ></imgBtn>
    </div>
  </div>
</template>

<script>
import TopHead from '../components/TopHead';

import imgBtn from '../components/imgBtn';
import service from '../server/service';
// Utility base64  blod
import Utility from '../common/Utility';
import canvasResize from 'canvas-resize';

import { Toast } from 'mint-ui';
export default {
  name: 'shopsignIn',
  created() {},
  data() {
    return {
      imgs: []
    };
  },
  methods: {
    //   
    submitSigin() {
      // ajax
      service
        .submitSigin({
          id: new Date(),
          shopId: this.$route.params.id, // id
          subOn: new Date().toLocaleDateString(), //'2019-07-31' 
          imgs: this.imgs.map(item => item.url) //url
        })
        .then(() => {
          Toast({
            message: '',
            position: 'middle',
            duration: 2000
          });
        })
        .catch(() => {
          Toast({
            message: '',
            position: 'middle',
            duration: 2000
          });
        });
    },
    //   
    removeImg() {
      this.imgs = this.imgs.filter(item => item.checked === false);
    },
    // 
    uploadImg() {
      let formData = new FormData();
      //   
      let file = this.$refs.file.files[0];
      //    base64 canvas-resize
      canvasResize(file, {
        crop: false, // 
        quality: 0.9, //   0 - 1
        rotate: 0, // 
        callback: baseStr => {
          // base64blod urlData, type
          let fileBlod = Utility.convertBase64UrlToBlob(baseStr, {
            type: file.type
          });
          formData.append('imgF', fileBlod);
          //   ajax
          service
            .uploadImg(formData)
            .then(res => {
              this.imgs.push({
                checked: false,
                url: `${process.env.VUE_APP_BASEURL}${res.data.img}`
              });
              console.log(res.data);
            })
            .catch(() => {
              console.log('');
            });
        }
      });
      //   console.log(this.$refs.file.files[0]);
    }
  },
  components: {
    TopHead,
    imgBtn
  }
};
</script>
<style lang="scss" scoped>
.shopsignIn {
  height: 100%;
  background-color: #fff;
}
.input-file {
  width: 0;
  height: 0;
  position: absolute;
  visibility: hidden;
}
.img-box-wrap {
  padding: px2rem(28);
  padding-bottom: 0;
  overflow: hidden;
  .img-box {
    position: relative;
    float: left;
    width: px2rem(220);
    height: px2rem(220);
    border: px2rem(1) solid #ccc;
    box-sizing: border-box;
    margin-right: px2rem(16);
    margin-bottom: px2rem(28);
    // 
    .imgchecked {
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: rgba(125, 125, 125, 0.4);
      i {
        font-size: px2rem(45);
        color: red;
      }
    }
  }
  .img-box:nth-child(3n) {
    margin-right: 0;
  }
}
</style>
