<template>
  <!--  -->
  <div class="login">
    <h1>SFA</h1>
    <!--  -->
    <div class="top_hat"></div>
    <!--  -->
    <div class="login-box">
      <!-- logo -->
      <div class="logo-wrap"><div class="logo-box"></div></div>
      <!--  -->
      <div
        class="input-group"
        :class="{ active: act_index === 1, error: errors.has('cno') }"
      >
        <label for="CNO"></label>
        <input
          name="cno"
          v-validate="{ required: true, max: 6, min: 4 }"
          @focus="act_index = 1"
          type="number"
          id="CNO"
          v-model="CNO"
        />
      </div>

      <div
        class="input-group"
        :class="{ active: act_index === 2, error: errors.has('pno') }"
      >
        <label for="PNO"></label>
        <input
          name="pno"
          v-validate="{ required: true, max: 12, min: 4 }"
          @focus="act_index = 2"
          type="number"
          id="PNO"
          v-model="PNO"
        />
      </div>

      <div
        class="input-group"
        :class="{ active: act_index === 3, error: errors.has('psw') }"
      >
        <label for="password"></label>
        <input
          name="psw"
          v-validate="{ required: true, max: 16, min: 8 }"
          @focus="act_index = 3"
          type="password"
          id="password"
          v-model="password"
        />
      </div>

      <!--  -->
      <div class="ck-row">
        <div class="ckbox_wrap" @click="remember" :class="{ active: rememb }">
          <i
            class="iconfont"
            :class="{ 'icon-border': !rememb, 'icon-check-square': rememb }"
          ></i>
          <span></span>
        </div>

        <div class="ckbox_wrap" @click="autoLogin" :class="{ active: autoLog }">
          <i
            class="iconfont"
            :class="{ 'icon-border': !autoLog, 'icon-check-square': autoLog }"
          ></i>
          <span></span>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="login-btn-wrap" @click="loginBtnClick"></div>
  </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import '../assets/font/iconfont.css';
// toast
import { Toast } from 'mint-ui';

// 
import { mapMutations } from 'vuex';

import service from '../server/service';

export default {
  name: 'login',
  data() {
    return {
      act_index: '1',
      // CNO PNO password input
      CNO: '',
      PNO: '',
      password: '',
      rememb: false,
      autoLog: false
    };
  },
  mounted() {
    // ,localStorage
    let data = JSON.parse(localStorage.getItem('Login_data'));
    // console.log(this.password);
    // console.log(sessionStorage);
    if (data) {
      this.CNO = data.CNO;
      this.PNO = data.PNO;
      this.password = data.password;
      this.rememb = data.rememb;
      this.autoLog = data.autoLog;
    } else {
      // url
      this.CNO = this.$route.query.CNO;
      this.PNO = this.$route.query.PNO;
    }

    //  
    this.$validator.validate();
    if (this.autoLog) {
      // 
      this.loginBtnClick();
    }
  },
  methods: {
    ...mapMutations(['initLoginUser']),
    // remembtruefalse,\
    remember() {
      this.rememb = !this.rememb;
      this.rememb || (this.autoLog = false);
    },
    // autoLogtruefalse,\
    autoLogin() {
      this.autoLog = !this.autoLog;
      // &&
      this.autoLog && (this.rememb = true);
    },
    // 
    loginBtnClick() {
      // 
      if (this.errors.any()) {
        return;
      }
      // ,2
      Indicator.open({
        text: '...',
        spinnerType: 'fading-circle'
      });

      //  ajax
      service
        .login({
          CNO: this.CNO,
          PNO: this.PNO,
          Passwd: this.password
        })
        .then(res => {
          if (res.data.code === 1) {
            // ,Login_data
            // ,JSON.stringify 
            localStorage.setItem(
              'Login_data',
              JSON.stringify({
                // 
                rememb: this.rememb,
                autoLog: this.autoLog,
                // 
                PNO: this.rememb ? this.PNO : '',
                CNO: this.rememb ? this.CNO : '',
                password: this.rememb ? this.password : ''
              })
            );
            // session
            sessionStorage.setItem('user_data', JSON.stringify(res.data.user));
            // token
            sessionStorage.setItem('LoginToken', res.data.token);

            // vuex,commit() mutations,state
            // vuex
            this.initLoginUser(res.data.user);

            // 
            this.$router.push('/home');
          } else {
            // 
            Toast({
              message: ',',
              position: 'middle',
              duration: 2000
            });
          }
          Indicator.close(); //
        })
        .catch(e => {
          console.log('', e);
          Toast({
            message: '',
            position: 'middle',
            duration: 2000
          });
          Indicator.close();
        });
    }
  }
};
</script>

<style lang="scss" scoped>
h1 {
  text-align: center;
  font-size: px2rem(36);
  height: px2rem(36);
  line-height: px2rem(36);
  padding: px2rem(148-44-36) 0 px2rem(44) 0;
  color: #ffffff;
}

// css
.login {
  height: 100%;
  background-color: #29d565;
  // css
  .top_hat {
    width: px2rem(537);
    height: px2rem(18);
    margin: 0 auto;
    background-color: #ededed;
    border-radius: px2rem(18) px2rem(18) 0 0;
  }

  @mixin login-wrap() {
    width: px2rem(600);
    margin: 0 auto;
    border-radius: px2rem(20);
    background-color: #fff;
  }

  // css
  .login-box {
    height: px2rem(836);
    @include login-wrap();
    // logocss
    .logo-wrap {
      padding: px2rem(80) 0;
      .logo-box {
        width: px2rem(190);
        height: px2rem(190);
        margin: 0 auto;
        background: url(../assets/logo.jpg) no-repeat;
        background-size: cover;
      }
    }
    @mixin rowStyle() {
      width: px2rem(401);
      padding: 0 px2rem(36);
      color: $text-color;
    }
    // inputcss
    .input-group {
      border: 2px solid #e2e2e2;
      border-radius: px2rem(45);
      height: px2rem(90);
      margin: 0 auto;
      line-height: px2rem(90);
      font-size: $text-size-mid;
      @include rowStyle();
      margin: 0 auto px2rem(30);
      input {
        border: 0 none;
        font-size: $text-size-mid;
        line-height: px2rem(90);
        width: px2rem(200);
        padding-left: 1em;
      }
    }
    // inputcss
    .input-group.active {
      color: $act-color;
      border: 2px solid $act-color;
    }
    // inputcss
    .input-group.error {
      color: $act-color;
      border: 2px solid red;
    }

    // css
    .ck-row {
      text-align: center;
      font-size: $text-size;
      display: flex;
      justify-content: space-around;
      // inputcss
      @include rowStyle();
      .ckbox_wrap {
        padding-top: px2rem(8);
        padding-left: px2rem(36);
        i::before {
          display: inline-block;
          width: px2rem(30);
          height: px2rem(30);
          font-size: px2rem(30);
        }
      }
      // checkbox 
      .ckbox_wrap.active {
        color: $act-color;
      }
    }
  }
  // css
  .login-btn-wrap {
    text-align: center;
    font-size: $text-size-imp;
    height: px2rem(100);
    line-height: px2rem(100);
    @include login-wrap();
    margin-top: px2rem(20);
    font-weight: 700;
    letter-spacing: px2rem(10);
  }
}
</style>
<style lang="scss">
body,
html,
#app {
  height: 100%;
  font-size: 12px;
}
</style>
