import Vue from 'vue';
import Router from 'vue-router';
import login from './views/login.vue';

// home
import Home from './views/Home.vue';
// user
import user from './views/user.vue';

import notice from './views/notice.vue';

import NoticeDetail from './views/NoticeDetail.vue';
import visitshop from './views/Visitshop.vue';
import shopInfo from './views/shopInfo.vue';
import shopsignIn from './views/shopsignIn.vue';
import order from './views/order.vue';
import goodsSlected from './views/goodsSlected.vue';
import card from './views/card.vue';
import CartRemark from './views/CartRemark.vue';
import SubCart from './views/SubCart.vue';
import addedshop from './views/addedshop.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      // home
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      // user
      path: '/user',
      name: 'user',
      component: user
    },
    {
      // 
      path: '/notice',
      name: 'notice',
      component: notice
    },
    {
      // id
      path: '/notice/:id',
      name: 'NoticeDetail',
      component: NoticeDetail
    },

    {
      // 
      path: '/visitshop',
      name: 'visitshop',
      component: visitshop
    },
    {
      // id
      path: '/visitshop/:id',
      name: 'shopInfo',
      component: shopInfo
    },
    {
      // 
      path: '/shopsignIn/:id',
      name: 'shopsignIn',
      component: shopsignIn
    },
    {
      // 
      path: '/order/:id',
      name: 'order',
      component: order
    },
    {
      // 
      path: '/goodsSlected/:id',
      name: 'goodsSlected',
      component: goodsSlected
    },
    {
      // 
      path: '/card/:id',
      name: 'card',
      component: card
    },
    {
      // 
      path: '/cartremark/:id',
      name: 'cartremark',
      component: CartRemark
    },
    {
      // 
      path: '/subcart',
      name: 'SubCart',
      component: SubCart
    },
    {
      // 
      path: '/addedshop',
      name: 'AddedShop',
      component: addedshop
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () =>
        import(/* webpackChunkName: "about" */ './views/About.vue')
    },
    {
      // 
      path: '*',
      name: 'all',
      component: visitshop
    }
  ]
});
