<template>
  <div class="addedshop">
    <TopHead title="">
      <div class="r-title" slot="r"></div>
    </TopHead>
    <div class="map-wrap" ref="map"></div>
    <div class="map-address">{{ address }}</div>
  </div>
</template>

<script>
import TopHead from '../components/TopHead';
export default {
  name: 'addedshop',
  data() {
    return {
      address: ''
    };
  },
  mounted() {
    this.initMap();
  },
  methods: {
    initMap() {
      // 
      let center = new qq.maps.LatLng(39.916527, 116.397128);
      // 
      var map = new qq.maps.Map(this.$refs.map, {
        center,
        zoom: 13, // 
        mapStyleId: 'style2' // keystyle1
      });
      // 
      var marker = new qq.maps.Label({
        //Marker
        position: center,
        //Marker
        map: map,
        content: '<p class="marker"></p>',
        //MarkerMap
        animation: qq.maps.MarkerAnimation.DROP,
        visible: true
      });
      // api
      let geocoder = new qq.maps.Geocoder();
      //   
      geocoder.setComplete(result => {
        // map.setCenter(result.detail.location);
        // 
        marker.setPosition(result.detail.location); //
        //,dateaddress
        this.address = result.detail.address;
      });

      //  
      geocoder.getAddress(center);

      qq.maps.event.addListener(map, 'click', event => {
        center = new qq.maps.LatLng(
          event.latLng.getLat(),
          event.latLng.getLng()
        );
        // map.setCenter(center);
        marker.setPosition(center);
        geocoder.getAddress(center);
      });
    }
  },
  components: {
    TopHead
  }
};
</script>
<style lang="scss" scoped>
.addedshop {
  height: 100%;
  background-color: #fff;
  .map-wrap {
    height: px2rem(360);
    border-bottom: 1px solid #ccc;
    // .marker {
    //
    //   font-size: px2rem(30);
    // }
  }
  .map-address {
    height: px2rem(120);
    line-height: px2rem(120);
    font-size: $text-size;
    color: $text-color-imp;
    background-color: $src-bg;
    padding: 0 px2rem(28);
  }
}
</style>
<style lang="scss">
// 
.marker {
  font-size: px2rem(30);
}
</style>
